:root {
  --font-body: "Heebo", sans-serif;
  --font-para:rem(16);
  --font-weight-Para:100;
  --font-heading-desktop:rem(44);
  --font-heading-mobile:rem(32);
  --font-button-text:rem(20);
}

:root {
  --white: hsl(0, 0%, 100%);
  --black: hsl(0, 0%, 0%);
  --black:#000;
  --button-color:#FF6464;
  --anchor-color:#00A8CC;
  --primary-bg:#E5E5E5;
  --secondary-bg:#EDF7FA;
  --white:#fff;
}

html {
  font-size: 100%;
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  margin: 0;
  padding: 0;
  font-family: var(--font-body);
}

h1,
h2,
h3 {
  margin-top: 0;
  line-height: 1.1;
}

a,
a:visited,
a:active {
  text-decoration: none;
}

.flex {
  display: flex;
}
.flex__jc-sb {
  justify-content: space-between;
}
.flex__jc-c {
  justify-content: center;
}
.flex__ai-c {
  align-items: center;
}
.flex__jc-end {
  justify-content: flex-end;
}

.bold {
  font-size: 1.375rem;
  font-weight: 700;
}
@media (min-width: 71.875em) {
  .bold {
    font-size: 1.625rem;
  }
}

.para {
  font-size: 1rem;
  font-weight: 400;
  color: gray;
}

.special {
  background-color: #142850;
  color: white;
  text-align: center;
  width: 3.75rem;
  border-radius: 1.875rem;
  display: inline-block;
  margin: 0 0.625rem 0 0.3125rem;
}

.works {
  font-size: 1.375rem;
}

.grid {
  display: grid;
  grid-template-rows: repeat(2, 1fr);
}
.grid__ji-c {
  justify-items: center;
}
.grid__ai-c {
  align-items: center;
}
@media (min-width: 71.875em) {
  .grid {
    grid-template-columns: repeat(2, 1fr);
  }
}

@media (max-width: 71.8125em) {
  .hide-for-mobile {
    display: none;
  }
}

@media (min-width: 71.875em) {
  .hide-for-desktop {
    display: none;
  }
}

.header {
  height: 6.35vh;
  width: 100%;
}
@media (min-width: 71.875em) {
  .header__nav {
    margin-top: 1.6875rem;
    margin-right: 3.75rem;
  }
}
.header__nav a {
  color: black;
  font-weight: 500;
  font-size: 1.25rem;
}
.header__nav a:not(:last-child) {
  margin-right: 2.5rem;
}
.header__nav--hamburger {
  margin-right: 1.125rem;
  margin-top: 1.25rem;
}
.header__nav--hamburger span {
  display: block;
  background-color: gray;
  width: 1.625rem;
  height: 0.125rem;
  margin-top: 0.5rem;
}

@media (max-width: 71.8125em) {
  .hero {
    margin-top: 2.25rem;
  }
  .hero img {
    margin-bottom: 2.25rem;
  }
  .hero__info {
    text-align: center;
  }
  .hero__info p:nth-child(1) {
    font-weight: 700;
    font-size: 2rem;
    padding: 0 1.25rem;
  }
  .hero__info p:nth-child(2) {
    color: gray;
    font-weight: 400;
    font-size: 1rem;
    padding: 0 1.5rem;
  }
  .hero__info--btn {
    margin-top: 1.6875rem;
    width: 13rem;
    height: 2.9375rem;
    background-color: var(--button-color);
    color: #fff;
    border-style: none;
  }
}
@media (min-width: 71.875em) {
  .hero {
    margin: 0 11.4375rem;
    height: 70vh;
  }
  .hero img {
    order: 2;
    margin-top: 9.5rem;
    padding-left: 11.4375rem;
  }
  .hero__info {
    margin-top: 9.5rem;
    order: 1;
    align-self: center;
  }
  .hero__info p:nth-child(1) {
    color: black;
    font-size: 2.75rem;
    font-weight: 700;
  }
  .hero__info p:nth-child(2) {
    color: gray;
    font-weight: 400;
    line-height: 1.4375rem;
    font: 1rem;
  }
  .hero__info--btn {
    margin-top: 2.375rem;
    width: 13rem;
    height: 2.9375rem;
    background-color: var(--button-color);
    color: #fff;
    border-style: none;
  }
}

.posts {
  background-color: var(--secondary-bg);
}
@media (max-width: 71.8125em) {
  .posts {
    margin-top: 3.75rem;
    padding: 1.375rem 1.25rem;
  }
  .posts__top {
    justify-content: center;
    padding: 0 1.0625rem;
  }
  .posts__grid {
    gap: 0.625rem 0;
    height: 100%;
  }
  .posts__grid--one, .posts__grid--two {
    padding: 1.25rem;
    background-color: white;
  }
}
@media (min-width: 71.875em) {
  .posts {
    margin-top: 4.375rem;
    height: 60vh;
    padding: 1.6875rem 9.125rem 0 9.125rem;
  }
  .posts__top a {
    color: #00A8CC;
    text-decoration: underline;
  }
  .posts__grid {
    gap: 1.25rem;
    height: 40vh;
  }
  .posts__grid--one, .posts__grid--two {
    padding: 1.25rem;
    background-color: white;
  }
}

.feature hr {
  border-top-style: none;
  opacity: 0.4;
  color: gray;
  height: 1px;
  width: 60%;
}
@media (max-width: 71.8125em) {
  .feature .works {
    text-align: center;
  }
  .feature__image {
    text-align: center;
  }
  .feature__dashboard--description, .feature__portraits--description, .feature__malayalam--description {
    margin-top: -20px;
    padding-left: 2rem;
    padding-right: 1.75rem;
  }
  .feature__dashboard--description .para, .feature__portraits--description .para, .feature__malayalam--description .para {
    font-size: 1rem;
  }
}
@media (min-width: 71.875em) {
  .feature {
    padding: 1.875rem 9.0625rem 9.0625rem 16.875rem;
  }
  .feature__dashboard, .feature__portraits, .feature__malayalam {
    height: 25vh;
    grid-template-columns: 246px 1fr;
  }
  .feature__dashboard--description, .feature__portraits--description, .feature__malayalam--description {
    padding-left: 1.125rem;
  }
}

.footer {
  text-align: center;
  margin-top: 3.75rem;
}
@media (min-width: 71.875em) {
  .footer {
    margin-top: -100px;
  }
}
.footer .footer__socials {
  width: 30%;
  margin: auto auto;
  display: grid;
  justify-items: center;
  grid-template-columns: repeat(4, 1fr);
}
@media (min-width: 71.875em) {
  .footer .footer__socials a img {
    width: 1.875rem;
    height: 1.875rem;
  }
}
@media (max-width: 71.8125em) {
  .footer .footer__socials {
    width: 80%;
    gap: 0 1.25rem;
  }
  .footer .footer__socials a img {
    width: 2.1875rem;
    height: 2.1875rem;
  }
}